import { useNavigate } from "react-router-dom"
import { Header, HeaderSearchBar } from "@rp/react/web/common/layout"
import { Button, Typography } from "@rp/react/web/mui/core"
import { Add } from "@rp/react/web/mui/icons"
import { BRIEF_ROUTE_CREATE } from "@rp/react/web/router"

type BriefDashHeaderProps = {
  filterTerm: string
  setFilterTerm: (term: string) => void
}

const BriefDashHeader = ({ filterTerm, setFilterTerm }: BriefDashHeaderProps) => {
  const navigate = useNavigate()

  return (
    <Header
      leftColumns={3}
      rightColumns={9}
      leftContent={<Typography sx={{ fontSize: 20, fontWeight: 700 }}>Briefs</Typography>}
      rightContent={
        <>
          <HeaderSearchBar
            key="header-search-bar"
            filterTerm={filterTerm}
            onChange={setFilterTerm}
            placeholder="Find your briefs"
          />
          <Button
            aria-label="create new brief"
            startIcon={<Add />}
            variant="contained"
            onClick={() => navigate(BRIEF_ROUTE_CREATE)}
          >
            New Brief
          </Button>
        </>
      }
    />
  )
}

export { BriefDashHeader }
